.dx-pager {
  display: flex;
  justify-content: space-between;
  user-select: none;
}

.dx-pager-page-size {
  display: flex;
}

.dx-pager-page-size__item {
  color: #333;
  border-radius: 3px;
  padding: 7px 9px 8px 10px;
  cursor: pointer;
}
.dx-pager-page-size__item.-selected {
  background-color: #d4d4d4;
}

.dx-pager-pages {
  display: flex;
}

.dx-pager-pages {
  display: flex;
}
.dx-pager-pages__item {
  color: #333;
  border-radius: 3px;
  padding: 7px 9px 8px 10px;
  cursor: default;
}
.dx-pager-pages__item.-selected {
  background-color: #d4d4d4;
}
.dx-pager-pages__item.-selectable {
  cursor: pointer;
}